.active {
  transition: all 0.5s;
  background: #0f383ca5;
  backdrop-filter: blur(10px);
}
.Avatar:hover {
  box-shadow: 0px 0px 10px #00f4ff;
  rotate: 360deg;
  transition: all 0.6s;
}

.hoverunderlineanimation {
  display: inline-block;
  position: relative;
  color: rgb(183, 175, 175);
}

.hoverunderlineanimation::after {
  content: "";
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #00f4ff;
  transform-origin: left right;
  transition: transform 0.25s ease-out;
}

.hoverunderlineanimation:hover::after {
  transform: scaleX(1);
  transform-origin: bottom left;
}
.selected {
  color: red;
}
